import _ from 'lodash';
import React, { useCallback } from "react";

const viewportContext = React.createContext({});

export const ViewportProvider = ({ children }) => {
    const [hasRan, setHasRan] = React.useState(false)
    const [screenSize, setScreenSize] = React.useState({
        height: 0,
        width: 0,
        scrollTop: 0
    })
    const handleWindowResize = useCallback(() => {
        setScreenSize({
            width: window.innerWidth,
            height: window.innerHeight,
            scrollTop: document.documentElement.scrollTop || document.body.scrollTop
        })
    }, []);
    React.useEffect(() => {
        if (!hasRan) {
            setHasRan(true)
            handleWindowResize()
        }
        const sc = _.throttle(handleWindowResize, 100);
        window.addEventListener('scroll', sc);
        window.addEventListener("resize", handleWindowResize);
        return () => {
            window.removeEventListener("scroll", sc);
            window.removeEventListener("resize", sc);
        }
    }, [hasRan, setHasRan]);

    return (
        <viewportContext.Provider value={{ ...screenSize }}>
            {children}
        </viewportContext.Provider>
    );
};

export const useViewport = () => {
    const value = React.useContext(viewportContext);
    const { width, height, scrollTop } = value;
    const size = width < 600 ? 's' : width < 960 ? 'm' : 'l';
    return { width, height, scrollTop, size };
}